@import '../styles/main.scss';

.header {
	background-color: white;
}

.main {
	position: relative;
}

.loader {
	display: block;
	margin: 40vh auto;
	width: 50px;
	height: 50px;
	animation: spin 1s linear infinite;
}

@keyframes spin {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
}